*{
    margin:0;
    padding:0;
}

.data_display{
    background-color: rgb(94, 94, 211);
    color:rgb(244, 226, 245);
    text-align: center;
}

.add_btn, .delete, .edit{
    border-radius: 10px;
    background-color:  rgb(94, 94, 211) ;
    font-size: 20px;
    color: rgb(244, 226, 245);
    border: none;
    margin-left: 30px;
    margin-top: 10px;
    padding: 5px;
    cursor: pointer;

}

input{
    font-size: 20px;
    padding: 5px;
    color: rgb(94, 94, 211) ;
    outline: none;
    margin-right: 10px;
    
}

.show{
    color: rgb(94, 94, 211) ;
    margin-top: 20px;
    margin-bottom: 20px;
}

th{
  font-size: 20px;
  color: blue;  
  margin-right: 10px;
}

td{
    font-size: 20px;
    color: rgb(94, 94, 211) ;
}